import { motion } from 'framer-motion'
import HotOfferButton from '../HotOfferButton/HotOfferButton';
import '../HotOfferSection/HotOfferSection.scss'

const HotOfferSection = () => {
    return(
        <motion.section 
            className='hotOfferSection'
            initial={{ opacity: 0, y: 50 }}          // изначально скрыт и чуть ниже
            animate={{ opacity: 1, y: 0 }}           // становится видимым и поднимается на место
            transition={{ duration: 1, ease: "easeOut" }} // плавность 2 секунды
        >
            <div className='container'>
                <div className='hotOfferTitles'>
                    <h5>Не пропусти</h5>
                    <h3>Скоро стартуем</h3>
                </div>
                <HotOfferButton />
            </div>
        </motion.section>
    )
}

export default HotOfferSection;
